*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100vw;
  font-family: "montserrat", sans-serif;
  background-color: #f4f4f4;
  color: #141414;
  position: relative;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
}

.GroundZero {
  max-width: 1920px;
  display: flex;
  justify-content: center;
  position: relative;
}

.Content_Wrap {
  width: 70%;
  margin-top: 100px;
  z-index: 20;
  position: relative;
}

a {
  text-decoration: none;
  color: #141414;
}

ul {
  list-style-type: none;
}

@media screen and (max-width: 1100px) {
  .Content_Wrap {
    width: 100%;
    padding: 0 80px;
  }
}
@media screen and (max-width: 700px) {
  .Content_Wrap {
    width: 100%;
    padding: 0 30px;
  }
}

